import { defineComponent, ref, reactive } from 'vue';
import { rules } from '../login-config/Phone';
export default defineComponent({
  name: 'LoginPhone',
  emits: ['changeActive'],

  setup() {
    const message = reactive({
      phone: '',
      passCode: ''
    });
    const ruleFormRef = ref(null);
    return {
      message,
      ruleFormRef,
      rules
    };
  }

});